import React, {Component} from 'react';
import './App.css';

/* from material-ui */

import ExpansionPanel, {ExpansionPanelSummary, ExpansionPanelDetails} from 'material-ui/ExpansionPanel';
import Avatar from 'material-ui/Avatar';
import Typography from 'material-ui/Typography';
import ExpandMoreIcon from 'material-ui-icons/ExpandMore';
import Chip from 'material-ui/Chip';
import DeleteIcon from 'material-ui-icons/Delete';
import IconButton from 'material-ui/IconButton';
import Divider from 'material-ui/Divider';
import Tooltip from 'material-ui/Tooltip';
import Paper from 'material-ui/Paper';
import ListItemSecondaryAction from 'material-ui/List';
import List from 'material-ui/List';
import Grid from 'material-ui/Grid';

class Question extends Component {
  render() {

    const styles = theme => ({
      btns: {
        textAlign: "right"
      },
x1: {
  display: "flex-grow"
}
    });

    return (<div className="container">
      <ExpansionPanel>
        <ExpansionPanelSummary expandIcon={<ExpandMoreIcon />}>
          <div className="x1">
            <Typography >
              <Grid container="container" spacing={16}>
                <Grid item="item" xs={2}>
                  <IconButton>
                    <Avatar size={20}>Q</Avatar>
                  </IconButton>
                </Grid>
                <Grid item="item" xs={5}>
                  <div >
                  What are the different versions of HTML?hjghjbhj,b
                                </div>
                </Grid>
                <Grid className="btns" item="item" xs={5}>
                  <Chip label="HTML"/>
                  <IconButton aria-label="Comments">
                    <i className="material-icons">bug_report</i>
                  </IconButton>
                  <IconButton aria-label="Comments">
                    <i className="material-icons">edit</i>
                  </IconButton>
                  <Tooltip title="Delete" placement="bottom">
                    <IconButton aria-label="Delete">
                      <DeleteIcon/>
                    </IconButton>
                  </Tooltip>
                </Grid>
              </Grid>
            </Typography>
          </div>
        </ExpansionPanelSummary>
        <Grid container="container" spacing={12}>
          <ExpansionPanelDetails className="trans">

            <Typography spacing={12}>
              <Grid item="item" xs={1}>
                <IconButton>
                  <Avatar>A</Avatar>
                </IconButton>
              </Grid>
              <Grid item="item" xs={0}>
                HTML has many versions since it inceptions during 1990’s. HTML version are HTML, HTML+, HTML 2.0, HTML 3.2, HTML 4.0 and the latest version HTML 5.
              </Grid>
            </Typography>

          </ExpansionPanelDetails>
        </Grid>
      </ExpansionPanel>
    </div>);
  }
}

export default Question;



