import React, {Component} from 'react';
import PropTypes from "prop-types";
import {withStyles} from "material-ui/styles";
import Paper from "material-ui/Paper";
import Typography from "material-ui/Typography";
import {MuiThemeProvider, createMuiTheme} from "material-ui/styles";
import Input from "material-ui/Input";
import Button from 'material-ui/Button';
import Save from 'material-ui-icons/Save';
import TextField from 'material-ui/TextField';
import './App.css';
import Template from './template.js';
import InputChips from 'material-input-chips';
import Grid from 'material-ui/Grid';
const request = require('superagent');




const theme = createMuiTheme({
  palette: {
    type: "dark"
  },
  typography: {
    // Use the system font over Roboto.
    fontFamily: "-apple-system,system-ui,BlinkMacSystemFont," + '"Segoe UI",Roboto,"Helvetica Neue",Arial,sans-serif'
  }
});

const styles = theme => ({
  root: {
    flexGrow: 1
  },
  button: {
    margin: theme.spacing.unit,
    direction: 'row',
    justify: 'center',
    alignItems: 'center'
  },
  leftIcon: {
    marginRight: theme.spacing.unit
  },
  iconSmall: {
    fontSize: 20
  },
  root: theme.mixins.gutters({
    paddingTop: 16,
    paddingBottom: 16,
    marginTop: theme.spacing.unit * 3
  })
});

class CreateTemplate extends Component {
  constructor(props) {
    super(props)
    this.state = {
      template_data: {
        "frontend": [
          "react", "nodejs", "html", "css"
        ],
        "backend": ["nodejs", "python", "java"]
      },


    }
    this.handleClick = this.handleClick.bind(this);
  }
 handleClick(){

   let title = document.getElementById('title').value;
   let skill = document.getElementById('skill').value;
   let template_data = Object.assign({},this.state.template_data);
   template_data[title] = [];
   template_data[title].push(skill);

   this.setState({template_data : template_data});

    }
  render() {
    const items = [];

    for (let key in this.state.template_data) {
      items.push(<Template title={key} skill={this.state.template_data[key]}/>)
      console.log(this.state.template_data[key]);
    }
    const {classes} = this.props;
    return (<div className="container">
      <MuiThemeProvider theme={theme}>
        <Paper className={classes.root} elevation={9}>
          <Typography variant="title" gutterBottom="gutterBottom" align="center">
            Create Template
          </Typography>

          <Input fullWidth="fullWidth" placeholder="Enter Title" className={classes.input} id="title" inputProps={{
              "aria-label" : "Description"
            }}/>

            <Input fullWidth="fullWidth" placeholder="Add skill" className={classes.input} id="skill" inputProps={{
                "aria-label" : "Description"
              }}/>

          <Button className={classes.button} variant="raised" size="small" onClick={this.handleClick}>
            <Save className={classes.leftIcon}/>
            Create
          </Button>

        </Paper>
        <div className={classes.root}>
          <Grid container="container" spacing={12} justify="space-around">
            {items}
          </Grid>
        </div>
      </MuiThemeProvider>
    </div>);
  }
}
CreateTemplate.propTypes = {
  classes: PropTypes.object.isRequired
};

export default withStyles(styles)(CreateTemplate);



