import React from 'react';
import PropTypes from 'prop-types';
import { withStyles } from 'material-ui/styles';
import classNames from 'classnames';
import Drawer from 'material-ui/Drawer';
import AppBar from 'material-ui/AppBar';
import Toolbar from 'material-ui/Toolbar';
import List, {ListItem, ListItemIcon, ListItemText} from 'material-ui/List';
import Typography from 'material-ui/Typography';
import Divider from 'material-ui/Divider';
import IconButton from 'material-ui/IconButton';
import MenuIcon from 'material-ui-icons/Menu';
import ChevronLeftIcon from 'material-ui-icons/ChevronLeft';
import ChevronRightIcon from 'material-ui-icons/ChevronRight';
import {BrowserRouter, Switch, Route,HashRouter as Router, Link} from 'react-router-dom';
import Question_Authoring from './Question_Authoring';
import CreateTemplate from './CreateTemplate';

import InboxIcon from 'material-ui-icons/Inbox';
import Tooltip from 'material-ui/Tooltip';
import Button from 'material-ui/Button';


import Editquestion from './Editquestion';

import TextField from 'material-ui/TextField';
//import { mailFolderListItems, otherMailFolderListItems } from './tileData';

const drawerWidth = 240;
const btn_style = {
	padding: '0 0 0 0'
}
const styles = theme => ({
  root: {
    flexGrow: 1,
    height: '100%',
    zIndex: 1,
    overflow: 'hidden',
    position: 'relative',
    display: 'flex',
  },
  appBar: {
    zIndex: theme.zIndex.drawer + 1,
    transition: theme.transitions.create(['width', 'margin'], {
      easing: theme.transitions.easing.sharp,
      duration: theme.transitions.duration.leavingScreen,
    }),
  },
  appBarShift: {
    marginLeft: drawerWidth,
    width: `calc(100% - ${drawerWidth}px)`,
    transition: theme.transitions.create(['width', 'margin'], {
      easing: theme.transitions.easing.sharp,
      duration: theme.transitions.duration.enteringScreen,
    }),
  },
  menuButton: {
    marginLeft: 12,
    marginRight: 36,
  },
  hide: {
    display: 'none',
  },
  drawerPaper: {
    position: 'relative',
    width: drawerWidth,
    transition: theme.transitions.create('width', {
      easing: theme.transitions.easing.sharp,
      duration: theme.transitions.duration.enteringScreen,
    }),
  },
  drawerPaperClose: {
    overflowX: 'hidden',
    transition: theme.transitions.create('width', {
      easing: theme.transitions.easing.sharp,
      duration: theme.transitions.duration.leavingScreen,
    }),
    width: theme.spacing.unit * 7,
    [theme.breakpoints.up('sm')]: {
      width: theme.spacing.unit * 9,
    },
  },
  toolbar: {
    display: 'flex',
    alignItems: 'center',
    justifyContent: 'flex-end',
    padding: '0 8px',
    ...theme.mixins.toolbar,
  },
  content: {
    flexGrow: 1,
    backgroundColor: theme.palette.background.default,
    padding: theme.spacing.unit * 3,
  },
});

class Header extends React.Component {
  state = {
    open: false,
  };

  handleDrawerOpen = () => {
    this.setState({ open: true });
  };

  handleDrawerClose = () => {
    this.setState({ open: false });
  };

  render() {
    const { classes, theme } = this.props;

    return (
      <div className={classes.root}>
        <AppBar
          position="absolute"
          className={classNames(classes.appBar, this.state.open && classes.appBarShift)}
        >
          <Toolbar disableGutters={!this.state.open}>
            <IconButton
              color="inherit"
              aria-label="open drawer"
              onClick={this.handleDrawerOpen}
              className={classNames(classes.menuButton, this.state.open && classes.hide)}
            >
              <MenuIcon />
            </IconButton>
            <Typography variant="title" color="inherit" noWrap>
              SAPIENT QUESTION AUTHORING PORTAL
            </Typography>
          </Toolbar>
        </AppBar>
        <Drawer
          variant="permanent"
          classes={{
            paper: classNames(classes.drawerPaper, !this.state.open && classes.drawerPaperClose),
          }}
          open={this.state.open}
        >
          <div className={classes.toolbar}>
            <IconButton onClick={this.handleDrawerClose}>
              {theme.direction === 'rtl' ? <ChevronRightIcon /> : <ChevronLeftIcon />}
            </IconButton>
          </div>
          <Divider />
          
          <List>
          	<ListItem>
          	  	<ListItemIcon>
			            <InboxIcon />
			    </ListItemIcon>
			    <ListItemText>
          			<List>
          			<ListItem>
				  		<TextField
				          id="Skill"
				          label="Name"
				          
				          margin="normal"
				        />
				    </ListItem>
			        <ListItem >
          			 <Button style = {btn_style} variant="raised">
				       Add Skill
				      </Button>
				     </ListItem>
				     </ List>
				</ListItemText>
	        </ListItem>
	       

          </List>
          <Divider />
         
          <List>
          	<ListItem button component = {Link} to = "/home/question_authoring" >
          	  
          			<ListItemIcon>
			            <InboxIcon />
			         </ListItemIcon>
			  		
          			<ListItemText>
          			Add Quiz
          			</ListItemText>
          			
          			

	        </ListItem>
	       

          </List>
          <Divider />
       
          <List>
          	<ListItem button component = {Link} to = "/home/create_template">
          <ListItemIcon>
            <InboxIcon />
          </ListItemIcon>
          <ListItemText primary="Add Template" />
        </ListItem>
        <ListItem button>
          <ListItemIcon>
            <InboxIcon />
          </ListItemIcon>
          <ListItemText primary="View Template" />
        </ListItem>

          </List>
         
        </Drawer>
        <main className={classes.content}>
          <div className={classes.toolbar} />
          
         	<Switch>
          	
          		<Route path = "/home/question_authoring" component = {Editquestion}/>
          		<Route path = "/home/create_template" component = {CreateTemplate}/>

          	</Switch>
         
        </main>
       
      </div>
    );
  }
}

Header.propTypes = {
  classes: PropTypes.object.isRequired,
  theme: PropTypes.object.isRequired,
};

export default withStyles(styles, { withTheme: true })(Header);



